<template>
	<view class="index">
		<scroll-view class="personal-index" scroll-y="true">
			<!-- 轮播图 -->
			<home-swiper />
			<!-- 我要兑换 && 查看兑换点 -->
			<view class="tools-button">
				<view class="tools-button-item" @click="goPages('/pages/personal/myCardBag/index?currTabs=0')">
					<image class="tbi-icon" src="../../../static/images/personal_icon_01.png" mode="aspectFill"></image>
					<view class="tbi-title">
						我要兑换
					</view>
					<view class="tbi-text">
						1元兑换一罐
					</view>
				</view>
				<view class="tools-button-item" @click="goPages('/pages/personal/shop/index')">
					<image class="tbi-icon" src="../../../static/images/personal_icon_02.png" mode="aspectFill"></image>
					<view class="tbi-title">
						查看兑换点
					</view>
					<view class="tbi-text">
						可查看附近的兑换店铺
					</view>
				</view>
			</view>
			<view class="index-page-list">
				<!-- 用户头像与名称 -->
				<view class="user-info">
					<view class="user-header">
						<image class="user-img" mode="aspectFill" :src="userInfo?userInfo.avatar_url:''" />
						<view class="user-info-details">
							<view class="user-info-name">{{userInfo?userInfo.nick_name:'未登录'}}
								<!-- <image class="sex" mode="aspectFill"  src="../../../static/images/vip.png" /> -->
							</view>
							<view class="user-info-id">
								ID：{{userInfo?userInfo.id:''}}
							</view>
						</view>
					</view>
					<view class="scan-scan"  @click="goPages('/pages/personal/sweepRingCode/sweepRingCode')">
						<image class="scan-icon" src="../../../static/images/personal_icon_scan.png" mode="aspectFill">
						</image>
						<view class="scan-text">
							扫一扫
						</view>
					</view>
				</view>
				<!-- 卡劵与积分 -->
				<view class="card-integral">
					<view class="card-integral-item" @click="goPages('/pages/personal/myCardBag/index?currTabs=0')">
						<image class="cii-icon" src="../../../static/images/personal_icon_03.png" mode="aspectFill">
						</image>
						<view class="cii-info">
							<view class="cii-num">{{statistics.unused}}</view>
							<view class="cii-text">可用兑换劵</view>
						</view>
					</view>
					<view class="card-integral-item" @click="goPages('/pages/personal/myPoints/index')">
						<image class="cii-icon" src="../../../static/images/personal_icon_04.png" mode="aspectFill">
						</image>
						<view class="cii-info">
							<view class="cii-num" v-if="userInfo">{{userInfo.credits|credits}}</view>
							<view class="cii-text">可用积分</view>
						</view>
					</view>
				</view>
				<!-- 四小模块 -->
				<view class="four-small-modules">
					<view class="fsm-item" @click="goShopMall">
						<image class="fsm-item-logo" src="../../../static/images/personal_icon_05.png"
							mode="aspectFill"></image>
						<view class="fsm-item-text">
							积分商城
						</view>
					</view>
					<view class="fsm-item" @click="goPages('/pages/personal/scanRecord/scanRecord')">
						<image class="fsm-item-logo" src="../../../static/images/personal_icon_06.png"
							mode="aspectFill"></image>
						<view class="fsm-item-text">
							扫码记录
						</view>
					</view>
					<view class="fsm-item"  @click="goPages('/pages/personal/welfare/index')">
						<image class="fsm-item-logo" src="../../../static/images/personal_icon_07.png"
							mode="aspectFill"></image>
						<view class="fsm-item-text">
							其它福利
						</view>
						<!-- 小红点 -->
						<view class="fsm-item-red animated rubberBand delay-1s" v-if="welfareTop.unused"></view>
					</view>
					<view class="fsm-item" @click="goPages('/pages/personal/aboutUs/index')">
						<image class="fsm-item-logo" src="../../../static/images/personal_icon_08.png"
							mode="aspectFill"></image>
						<view class="fsm-item-text">
							关于我们
						</view>
					</view>
				</view>

			</view>
			<!-- 底部 -->
			<view class="banner-ad">
				<ad unit-id="adunit-11a5f3c61ef44f16"></ad>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import {
		mapGetters,
		mapActions,
		mapMutations
	} from 'vuex'
	import {
		cscanQr
	} from '@/api/homeApi.js'
	import {
		getUserLocation
	} from '@/utils/getUserLocation.js'
	import {
		PLAQUEADVERTISING
	} from '@/utils/index.js'
	import homeSwiper from './homeSwiper.vue'
	import {
		setWxScanQrCode,
		getWxScanQrCode,
		setReferer
	} from '@/utils/auth.js'
	import {
		fileBaseUrl,baseUrl
	} from '@/utils/http.js'
	//插屏广告管理
	let _PLAQUEADVERTISING = null
	//防止测试快速点击
	let _oneClick = false
	export default {
		// 页面初始化 options为页面跳转所带来的参数
		onLoad(o) {	
			//插屏广告管理初始化
			_PLAQUEADVERTISING = PLAQUEADVERTISING()
			//扫码进入当前页获取 二维码携带信息 
			if (o && o.q) { //sid代表店铺ID
			   let _setReferer = 0
				if (new RegExp('sid').test(o.q)) {
					//店铺码不做处理
					_setReferer = 1
				} else { //拉环二维码
					let qr = decodeURIComponent(o.q)
					setWxScanQrCode(decodeURIComponent(o.q))
					//判断是否为拉环二维码
					if (RegExp('HTTP://4XV.CN').test(qr)) {
						_setReferer = 2
					}
				}
				//设置判断
				setReferer(_setReferer)
				//扫码进来的也要请求 获取广告
				this.getAdData(true).then(function(e) {})
			}
			//没有授权过，滚去重新授权
			if (!this.userInfo) {
				this.logout()
				return uni.redirectTo({
					url: '/pages/login/login'
				})
			}
			//更新顶部统计
			this.getCardTopCount()
			//获取福利统计
			this.getWelfareTop()
			//更新用户信息
			this.getUserInfo()
			//初始化广告
			_PLAQUEADVERTISING.init('adunit-2fc278170a691d0f')
		},
		components: {
			homeSwiper
		},
		onShow() {
			//恢复状态
			_oneClick = false
			this.isLocation = false
			//数据初始化
			this.initData()
		},
		onShareAppMessage(res) {
			if (res.from === 'button') { // 来自页面内分享按钮
				// console.log(res.target)
			}
			return {
				title: '彬纷享礼小程序，红牛一元轻松享',
				path: '/pages/tabBar/personal/index',
			  imageUrl:"https://file.y1b.cn/public/img/bfxn/202105/bfxl_wechatShare_new.png"
			}
		},
		filters:{
			credits(val){
				if(val&&val>9999){
					let data = parseFloat(999999/10000).toString().split('.')
					return data[0] + '万+'
				}
				return val
			}
		},
		computed: {
			...mapGetters(['userInfo', 'statistics', 'adData','token','welfareTop'])
		},
		data() {
			return {
				fileUrl: fileBaseUrl + '/public/img/bfxn/202101/',
				isLocation: false
			}
		},
		methods: {
			...mapActions({
				getCardTopCount: 'personal/getCardTopCount',
				getAdData: 'personal/getAdData',
				getUserInfo: 'login/getUserInfo',
				getWelfareTop: 'personal/getWelfareTop'
			}),
			...mapMutations({
				setUserLocation: 'login/setUserLocation',
				logout:'login/logout'
			}),
			initData() {
				//是否有用户信息
				if (this.userInfo) {
					//获取扫描的拉环码信息
					let wxScanQrCode = getWxScanQrCode()
					//如果登录过，刷新下经纬度
					getUserLocation().then(data => {
						//定位更新
						this.isLocation = true
						//更新用户定位信息
						if (!data.isold) this.setUserLocation(data.data)
						//处理用户通过微信扫描的罐码
						if (wxScanQrCode) {
							setTimeout(()=>{
								this.winningCheck(wxScanQrCode)
							},0)
							return 
						}
						//播放广告
						_PLAQUEADVERTISING.play()
					})
				}
			},
			goShopMall(){
				this.$router.navigateTo({
					url:'/pages/webview/webview?link=' + encodeURIComponent(baseUrl+'/api/txjifen/txlogin?way=2&token='+this.token+'&random='+Date.now())
				})
			},
			//跳转指定页面
			goPages(url) {
				//防止快速点击
				if (_oneClick) return
				//防止快速点击
				_oneClick = true 
				//没有定位直接去定位页
				if (!this.isLocation) {
					return uni.navigateTo({
						url: '/pages/noLocation/noLocation'
					})
				}
				this.$router.navigateTo({
					url
				})
			},
			winningCheck(wxScanQrCode) {
				//没有定位直接去定位页
				if (!this.isLocation) {
					return uni.navigateTo({
						url: '/pages/noLocation/noLocation'
					})
				}
				//统一去扫码页处理
				this.$router.redirectTo({
						url:`/pages/personal/sweepRingCode/sweepRingCode?wxScanQrCode=${wxScanQrCode}`
					})
			}
		}
	}
</script>

<style lang="scss">
	page {
		&::after {
			content: '';
			position: fixed;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 2rpx;
			box-shadow: 0px -1px 7px 0px rgba(192, 196, 204, 1);
			z-index: 9999;
		}
	}

	.personal-index {
		position: fixed;
		width: 100%;
		top: 0;
		bottom: 0;
		background-color: #eeeeef;
		z-index: 0;

		.tools-button {
			background: #ffffff;
			border-radius: 10px;
			box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.16);
			margin: -20rpx 20rpx 40rpx;
			position: relative;
			z-index: 1;
			display: flex;
			padding-top: 28rpx;
			padding-bottom: 20rpx;

			&::after {
				content: '';
				position: absolute;
				top: 30rpx;
				bottom: 30rpx;
				width: 2rpx;
				background-color: #EEEEEF;
				left: 50%;
				transform: translateX(-50%);
			}

			.tools-button-item {
				flex: 1;
				display: flex;
				flex-direction: column;
				align-items: center;
			}


			.tbi-icon {
				width: 110rpx;
				height: 110rpx;
			}

			.tbi-title {
				font-size: 32rpx;
				color: #333;
				font-weight: 800;
				margin-top: 10rpx;
			}

			.tbi-text {
				font-size: 20rpx;
				color: #999;
			}
		}

		.index-page-list {
			border-radius: 10px;
			box-shadow: 0px 6px 12px 0px rgba(0, 0, 0, 0.16);
			background-color: #FFFFFF;
			margin: 0 20rpx;
		}

		/*头部用户信息相关*/
		.user-info {
			padding: 44rpx 60rpx 40rpx 44rpx;
			display: flex;
			align-items: center;
			justify-content: space-between;
			position: relative;
		}

		.user-header {
			display: flex;
		}

		.user-img {
			width: 72rpx;
			height: 72rpx;
			border-radius: 50%;
			margin-right: 14rpx;
		}

		.user-info-details {
			width: 240rpx;
		}

		.user-info-name {
			color: #333;
			font-size: 32rpx;
			margin-bottom: 2rpx;
			width: 100%;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			font-weight: 700;
		}

		.user-info-id {
			color: #999;
			font-size: 24rpx;
		}

		.scan-scan {
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		.scan-icon {
			width: 40rpx;
			height: 40rpx;
		}

		.scan-text {
			color: #B48C33;
			font-size: 24rpx;
		}



		/*头部用户信息相关*/

		.card-integral {
			padding: 0 20rpx 60rpx;
			display: flex;
		}

		.card-integral-item {
			background: rgba(238, 238, 239, 0.40);
			border-radius: 5px;
			padding: 20rpx 0 20rpx 40rpx;
			flex: 1;
			display: flex;

			&:first-child {
				margin-right: 18rpx;
			}
		}

		.cii-icon {
			width: 80rpx;
			height: 80rpx;
			margin-right: 20rpx;
		}

		.cii-info {
			text-align: center;
		}

		.cii-num {
			font-size: 40rpx;
			color: #333;
		}

		.cii-text {
			font-size: 28rpx;
			color: #999;
		}

		.four-small-modules {
			display: flex;
			justify-content: space-between;
			padding: 0 60rpx 90rpx;
		}

		.fsm-item {
			text-align: center;
			font-size: 0;
			position: relative;
		}
		
		.fsm-item-red{
			width: 20rpx;
			height: 20rpx;
			background-color: #d81e06;
			border-radius: 50%;
			position: absolute;
			top:0;
			right: 15rpx;
		}

		.fsm-item-logo {
			width: 60rpx;
			height: 60rpx;
		}

		.fsm-item-text {
			font-size: 24rpx;
			color: #242424;
			margin-top: 5rpx;
		}

		.doll-bottom {}

		.doll-item {
			padding-left: 30rpx;
			width: 140rpx;
			height: 108rpx;
		}

		.banner-ad {
			width: 100%;
			margin-top: 30rpx;
		}
		
		


	}
</style>
